import { firestoreAction } from "vuexfire";
import { firestore } from "../config/firebase";

// bind summary
export const bindSummary = firestoreAction(({ bindFirestoreRef }) => {
  // return the promise returned by `bindFirestoreRef`
  return bindFirestoreRef(
    "summary",
    firestore.collection("summary").doc("latest")
  );
});

// bind unions
export const bindUnions = firestoreAction(({ bindFirestoreRef }) => {
  // return the promise returned by `bindFirestoreRef`
  return bindFirestoreRef(
    "unions",
    firestore.collection("unions").orderBy("name", "asc")
  );
});

// bind occupations
export const bindOccupations = firestoreAction(({ bindFirestoreRef }) => {
  // return the promise returned by `bindFirestoreRef`
  return bindFirestoreRef(
    "occupations",
    firestore.collection("occupations").orderBy("name", "asc")
  );
});

// bind patients
export const bindPatients = firestoreAction(({ bindFirestoreRef }) => {
  // return the promise returned by `bindFirestoreRef`
  return bindFirestoreRef(
    "patients",
    firestore.collection("patients").orderBy("infectedDate", "desc")
  );
});
