import Vue from "vue";
import VueRouter from "vue-router";
import { auth } from "../config/firebase";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Home",
    component: () => import("../views/Home")
  },
  {
    path: "/login",
    name: "Login",
    component: () => import("../views/Login"),
    // a meta field
    meta: { requiresGuest: true }
  },
  {
    path: "/dashboard",
    name: "Dashboard",
    component: () => import("../views/Dashboard"),
    // a meta field
    meta: { requiresAuthenticate: true },
    redirect: { name: "Dashboard.Summary" },
    children: [
      {
        path: "summary",
        name: "Dashboard.Summary",
        component: () => import("../views/dashboard/Summary")
      },
      {
        path: "unions",
        name: "Dashboard.Unions",
        component: () => import("../views/dashboard/Unions")
      },
      {
        path: "occupations",
        name: "Dashboard.Occupations",
        component: () => import("../views/dashboard/Occupations")
      },
      {
        path: "patients",
        name: "Dashboard.Patients",
        component: () => import("../views/dashboard/Patients")
      }
    ]
  }
];

const router = new VueRouter({
  mode: "hash",
  base: process.env.BASE_URL,
  //linkActiveClass: "active",
  routes
});

// authentication
router.beforeEach(async (to, from, next) => {
  const currentUser = await auth.currentUser;

  const requiresAuthenticate = to.matched.some(
    record => record.meta.requiresAuthenticate
  );
  const requiresGuest = to.matched.some(record => record.meta.requiresGuest);

  if (requiresAuthenticate) {
    if (currentUser) {
      next();
    } else {
      next({ name: "Login", query: { redirect: to.fullPath } });
    }
  } else if (requiresGuest && currentUser) {
    next({ name: "Dashboard" });
  } else {
    next();
  }
});

export default router;
