export default {
  /*=======================User Start=======================*/

  // specific user by uid
  getterSpecificUserByUid: state => {
    return state.currentUser;
  },
  // specific occupation by uid
  getterSpecificOccupationById: state => id => {
    return (state.occupations != null) ? state.occupations.find(occupation => occupation.id === id) : {};
  },
  // specific union by uid
  getterSpecificUnionById: state => id => {
    return (state.unions != null) ? state.unions.find(union => union.id === id) : {};
  }
};
