import { vuexfireMutations } from "vuexfire";

export default {
    ...vuexfireMutations,
    // loading route
    mutationLoadingRoute: (state, payload) => {
        state.loadingRoute = payload;
    },
    // set current user
    mutationCurrentUser: (state, payload) => {
        state.currentUser = payload;
    }
};
